.stopwatch {
  --time-value: 1;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch--speed-up {
  --time-value: 6;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  transform-origin: bottom;
  animation-name: tick-tock;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

@keyframes tick-tock {
  to {
    transform: rotate(360deg);
  }
}

.stopwatch__hand--minute {
  animation-duration: calc(3600s / var(--time-value));
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-left: -1.5vmin;
  animation-timing-function: steps(60, end);
}

.stopwatch__hand--second {
  animation-duration: calc(60s / var(--time-value));
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-left: -.75vmin;
  animation-timing-function: linear;
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
